<template>
  <div>
    <parallax class="section page-header header-filter" :style="headerStyle">
      <div class="map_wrap">
        <!-- 지도 -->
        <div
          id="map"
          style="width:100%;height:100%;position:relative;overflow:hidden;"
        ></div>
        <!-- 카테고리 선택 -->
        <ul id="category">
          <li id="BK9" data-order="0" @click="onClickCategory($event)">
            <span class="category_bg bank"></span>
            은행
          </li>
          <li id="MT1" data-order="1" @click="onClickCategory($event)">
            <span class="category_bg mart"></span>
            마트
          </li>
          <li id="PM9" data-order="2" @click="onClickCategory($event)">
            <span class="category_bg pharmacy"></span>
            약국
          </li>
          <li id="OL7" data-order="3" @click="onClickCategory($event)">
            <span class="category_bg oil"></span>
            주유소
          </li>
          <li id="CE7" data-order="4" @click="onClickCategory($event)">
            <span class="category_bg cafe"></span>
            카페
          </li>
          <li id="CS2" data-order="5" @click="onClickCategory($event)">
            <span class="category_bg store"></span>
            편의점
          </li>
        </ul>
        <!-- 범위 조절 슬라이더 -->
        <div id="slider">
          <div style="font-size:14px;">주변 상권 반경 설정</div>
          <vue-slider
            v-model="radius"
            @change="categoryRefresh"
            tooltipPlacement="bottom"
            tooltip="none"
            :min="0"
            :max="3000"
            style="width:175px; display: inline-block; vertical-align: middle;"
          ></vue-slider
          ><span
            style="vertical-align: middle; margin-left: 20px; font-size:14px;"
            >{{ radius }}m</span
          >
        </div>

        <!-- 시도/구군/동 선택창 -->
        <div class="md-layout" id="area">
          <b-form-select
            class="sel"
            name="sido"
            id="sido"
            v-model="sidoCode"
            :options="sidos"
            @change="gugunList"
          >
            <template #first>
              <b-form-select-option value="" disabled
                >시/도</b-form-select-option
              >
            </template>
          </b-form-select>
          <b-form-select
            class="sel"
            name="gugun"
            id="gugun"
            v-model="gugunCode"
            :options="guguns"
            @change="dongList"
          >
            <template #first>
              <b-form-select-option value="" disabled
                >구/군</b-form-select-option
              >
            </template>
          </b-form-select>
          <b-form-select
            class="sel"
            name="dong"
            id="dong"
            v-model="dongCode"
            :options="dongs"
            @change="moveMapByDong"
          >
            <template #first>
              <b-form-select-option value="" disabled>동</b-form-select-option>
            </template>
          </b-form-select>
        </div>

        <!-- 주소 검색창 -->
        <div id="area-search">
          <b-input-group>
            <template #append>
              <b-button @click="moveMap()" style="width: 30px;">
                <b-icon icon="search"></b-icon>
              </b-button>
            </template>
            <b-form-input
              style="border: 0; padding-left: 10px;"
              id="word"
              v-model="searchWord"
              placeholder="원하시는 지역명을 입력해주세요"
              @keyup.enter="moveMap()"
              @input="searchName($event)"
            ></b-form-input>
          </b-input-group>
          <b-list-group>
            <b-list-group-item
              style="padding: 10px;"
              v-for="(item, index) in searchResultList"
              :key="index"
              @click="selectSearchList(index)"
              >{{ item.address_name }}
            </b-list-group-item>
          </b-list-group>
        </div>

        <!-- 필터 버튼 -->
        <button id="filterButton" @click="filterShow = !filterShow">
          <b-icon icon="filter"></b-icon>
          필터
        </button>
        <!-- 필터창 -->
        <div id="filter" v-if="filterShow">
          <md-list>
            <md-list-item>
              <div>
                <b-icon
                  icon="x"
                  style="width:25px; height:25px; vertical-align:middle"
                  @click="filterShow = false"
                ></b-icon>
                <span
                  style="font-size: 15px; font-weight: bold; vertical-align:middle"
                  >필터</span
                >
              </div>
              <div>
                <p @click="resetFilter()">초기화</p>
              </div>
            </md-list-item>
            <md-divider></md-divider>
            <md-list-item>
              <div class="title">거래유형</div>
            </md-list-item>
            <md-list-item>
              <b-button
                variant="outline-secondary"
                @click="selectFilterHouseType('all')"
                :pressed="filterHouseType == 'all'"
                >전체</b-button
              >
              <b-button
                variant="outline-secondary"
                @click="selectFilterHouseType('apt')"
                :pressed="filterHouseType == 'apt'"
                >아파트</b-button
              >
              <b-button
                variant="outline-secondary"
                @click="selectFilterHouseType('officetel')"
                :pressed="filterHouseType == 'officetel'"
                >오피스텔</b-button
              >
            </md-list-item>
            <md-divider></md-divider>
            <md-list-item>
              <div class="title">건축년도</div>
            </md-list-item>
            <md-list-item>
              <b-button
                variant="outline-secondary"
                @click="selectFilterBuildYear(0)"
                :pressed="filterBuildYear == 0"
                >전체</b-button
              >
              <b-button
                variant="outline-secondary"
                @click="selectFilterBuildYear(5)"
                :pressed="filterBuildYear == 5"
                >5년 이내</b-button
              >
              <b-button
                variant="outline-secondary"
                @click="selectFilterBuildYear(10)"
                :pressed="filterBuildYear == 10"
                >10년 이내</b-button
              >
            </md-list-item>
            <md-list-item>
              <b-button
                variant="outline-secondary"
                @click="selectFilterBuildYear(15)"
                :pressed="filterBuildYear == 15"
                >15년 이내</b-button
              >
              <b-button
                variant="outline-secondary"
                @click="selectFilterBuildYear(20)"
                :pressed="filterBuildYear == 20"
                >20년 이내</b-button
              >
              <b-button
                variant="outline-secondary"
                @click="selectFilterBuildYear(25)"
                :pressed="filterBuildYear == 25"
                >25년 이내</b-button
              >
            </md-list-item>
          </md-list>
        </div>

        <!-- 우측 상세정보창 -->
        <div>
          <md-content id="result" v-if="house">
            <md-list-item>
              <div @click="initHouse()">
                <b-icon-arrow-left
                  style="width:20px; height:20px"
                ></b-icon-arrow-left>
              </div>
              <div v-if="userInfo">
                <b-icon
                  @click="addHouseInterest()"
                  v-if="!checkHouseInterest()"
                  icon="bookmark-heart"
                  style="width:20px; height:20px"
                ></b-icon>
                <b-icon
                  @click="removeHouseInterest()"
                  v-if="checkHouseInterest()"
                  icon="bookmark-heart-fill"
                  style="width:20px; height:20px"
                ></b-icon>
              </div>
            </md-list-item>
            <div class="title" v-text="house.houseName">아파트명</div>
            <md-list>
              <md-list-item>
                <div style="width: 60px;">건축년도</div>
                <div class="md-list-item-text">{{ house.buildYear }}</div>
              </md-list-item>
              <md-divider></md-divider>
              <md-list-item>
                <div style="width: 60px;">주소</div>
                <div class="md-list-item-text">
                  {{ house.sigungu }} {{ house.roadName }}
                </div>
              </md-list-item>
              <md-divider></md-divider>
              <md-list-item>
                <div class="md-list-item-text">실거래가</div>
                <!-- 전체, 매매, 전월세 필터링 -->
                <div>
                  <b-button
                    variant="outline-secondary"
                    style="width: 60px; height: 30px; margin-left: 5px;"
                    @click="selectFilterHouseDealType('all')"
                    :pressed="filterHouseDealType == 'all'"
                    >전체</b-button
                  >
                  <b-button
                    variant="outline-secondary"
                    style="width: 60px; height: 30px; margin-left: 5px;"
                    @click="selectFilterHouseDealType('sale')"
                    :pressed="filterHouseDealType == 'sale'"
                    >매매</b-button
                  >
                  <b-button
                    variant="outline-secondary"
                    style="width: 60px; height: 30px; margin-left: 5px;"
                    @click="selectFilterHouseDealType('etc')"
                    :pressed="filterHouseDealType == 'etc'"
                    >전월세</b-button
                  >
                </div>
              </md-list-item>
              <md-table>
                <md-table-row class="default-font-size center">
                  <md-table-cell>계약일</md-table-cell>
                  <md-table-cell>거래가격</md-table-cell>
                  <md-table-cell>종류</md-table-cell>
                  <md-table-cell>면적</md-table-cell>
                  <md-table-cell>층수</md-table-cell>
                </md-table-row>

                <md-table-row
                  v-for="houseDeal in filteredHouseDeals"
                  :key="houseDeal.dealId"
                  class="center"
                >
                  <md-table-cell
                    >{{ houseDeal.dealYear }}.{{ houseDeal.dealMonth }}.{{
                      houseDeal.dealDay
                    }}</md-table-cell
                  >
                  <md-table-cell>{{
                    houseDeal.dealType == "sale"
                      ? houseDeal.saleAmount
                      : houseDeal.dealType == "jeonse"
                      ? houseDeal.jeonseAmount
                      : houseDeal.monthlyDeposit + "/" + houseDeal.monthlyLease
                  }}</md-table-cell>
                  <md-table-cell>{{
                    houseDeal.dealType == "sale"
                      ? "매매"
                      : houseDeal.dealType == "jeonse"
                      ? "전세"
                      : "월세"
                  }}</md-table-cell>
                  <md-table-cell>{{ houseDeal.area }}m²</md-table-cell>
                  <md-table-cell>{{ houseDeal.floor }}층</md-table-cell>
                </md-table-row>
              </md-table>
            </md-list>
          </md-content>
        </div>
      </div>
    </parallax>
  </div>
</template>

<script>
import { mapActions, mapMutations, mapState } from "vuex";
import { KAKAO_MAP_JAVASCRIPT_KEY } from "@/config";
const houseStore = "houseStore";
const memberStore = "memberStore";
const interestStore = "interestStore";

export default {
  bodyClass: "landing-page",
  props: {
    header: {
      type: String,
      default: require("@/assets/img/bg7.jpg"),
    },
  },
  data() {
    return {
      map: null, // 카카오맵
      ps: null, // 장소검색 객체
      radius: 3000, // 카테고리 검색 범위 (기본 5000m. 0~20000)
      circle: null, // 반경 거리를 보여주는 원

      placeOverlay: null, // 장소 오버레이를 위한 객체
      contentNode: null, // 오버레이 컨텐츠 el

      markers: [], // 마커를 담을 배열 (카테고리)
      currCategory: "", // 현재 선택된 카테고리

      houseMarkers: [], // 집 마커

      sidoCode: "",
      gugunCode: "",
      dongCode: "",

      houseInterestInfo: null, // 선택한 집의 관심데이터 정보
      filterHouseDealType: "all", // 선택한 집 거래종류 필터링
      filteredHouseDeals: [], // 필터링된 집 거래정보

      filterShow: false,
      filterHouseType: "all",
      filterBuildYear: 0,

      searchWord: "", // 검색창 검색어
    };
  },
  created() {
    this.initHouse();

    this.CLEAR_SIDO_LIST();
    this.CLEAR_GUGUN_LIST();
    this.CLEAR_DONG_LIST();
    this.getSido();

    this.initResultList();

    this.filterShow = false;
    this.filterHouseDealType = "all";
  },
  mounted() {
    if (window.kakao && window.kakao.maps) {
      this.initMap();
    } else {
      const script = document.createElement("script");
      /* global kakao */
      script.onload = () => kakao.maps.load(this.initMap);
      script.src =
        // "//dapi.kakao.com/v2/maps/sdk.js?autoload=false&libraries=services&appkey=c5dd7f232900f6b19f29c968f087bb0f";
        "//dapi.kakao.com/v2/maps/sdk.js?autoload=false&libraries=services&appkey=" +
        KAKAO_MAP_JAVASCRIPT_KEY;
      document.head.appendChild(script);
    }
  },
  methods: {
    initMap() {
      // 1. 지도 등록
      const container = document.getElementById("map");
      const options = {
        center: new kakao.maps.LatLng(33.450701, 126.570667),
        level: 5,
      };

      //지도 객체를 등록합니다.
      //지도 객체는 반응형 관리 대상이 아니므로 initMap에서 선언합니다.
      this.map = new kakao.maps.Map(container, options);

      // 2. 카테고리별 장소 검색
      // 장소 검색 객체를 생성합니다
      this.ps = new kakao.maps.services.Places(this.map);

      // 지도에 idle 이벤트를 등록합니다
      kakao.maps.event.addListener(this.map, "idle", this.searchPlaces);

      // 마커 클릭시 커스텀 오버레이
      this.placeOverlay = new kakao.maps.CustomOverlay({ zIndex: 1 });
      this.contentNode = document.createElement("div"); // 커스텀 오버레이의 컨텐츠 엘리먼트 입니다.

      // 커스텀 오버레이의 컨텐츠 노드에 css class를 추가합니다
      this.contentNode.className = "placeinfo_wrap";

      // 커스텀 오버레이의 컨텐츠 노드에 mousedown, touchstart 이벤트가 발생했을때
      // 지도 객체에 이벤트가 전달되지 않도록 이벤트 핸들러로 kakao.maps.event.preventMap 메소드를 등록합니다
      this.addEventHandle(
        this.contentNode,
        "mousedown",
        kakao.maps.event.preventMap
      );
      this.addEventHandle(
        this.contentNode,
        "touchstart",
        kakao.maps.event.preventMap
      );

      // 커스텀 오버레이 컨텐츠를 설정합니다
      this.placeOverlay.setContent(this.contentNode);

      // 지도 중심좌표 변경 이벤트 등록
      kakao.maps.event.addListener(this.map, "center_changed", () => {
        // 지도의 중심좌표를 얻어옵니다
        var coords = this.map.getCenter();
        // 주소-좌표 변환 객체를 생성합니다
        var geocoder = new kakao.maps.services.Geocoder();
        geocoder.coord2RegionCode(
          coords.getLng(),
          coords.getLat(),
          this.getCenterAroundHouseInfo
        );
      });

      // 메인 페이지에서 검색해서 넘어왔으면 해당 좌표로 이동
      // console.log(this.$route.params.lat);
      if (this.$route.params.lat) {
        let pos = new kakao.maps.LatLng(
          this.$route.params.lat,
          this.$route.params.lng
        );

        // 지도 이동(기존 위치와 가깝다면 부드럽게 이동)
        this.map.panTo(pos);
      } else {
        // 아니면 현재 위치받아서 이동시킨다.
        navigator.geolocation.getCurrentPosition(
          this.locationLoadSuccess,
          this.locationLoadError
        );
      }
    },

    // 엘리먼트에 이벤트 핸들러를 등록하는 함수입니다
    addEventHandle(target, type, callback) {
      if (target.addEventListener) {
        target.addEventListener(type, callback);
      } else {
        target.attachEvent("on" + type, callback);
      }
    },

    // 카테고리 검색을 요청하는 함수입니다
    searchPlaces() {
      if (!this.currCategory) {
        return;
      }

      // 커스텀 오버레이를 숨깁니다
      this.placeOverlay.setMap(null);

      // 지도에 표시되고 있는 마커를 제거합니다
      this.removeMarker();
      // console.log(this.radius);
      this.ps.categorySearch(this.currCategory, this.placesSearchCB, {
        useMapCenter: true,
        radius: this.radius,
      });

      // 반경 원을 그려줍니다.
      // 지도에 표시할 원을 생성합니다
      const coords = this.map.getCenter();
      this.circle = new kakao.maps.Circle({
        center: new kakao.maps.LatLng(coords.getLat(), coords.getLng()), // 원의 중심좌표 입니다
        radius: this.radius, // 미터 단위의 원의 반지름입니다
        strokeWeight: 5, // 선의 두께입니다
        strokeColor: "#75B8FA", // 선의 색깔입니다
        strokeOpacity: 1, // 선의 불투명도 입니다 1에서 0 사이의 값이며 0에 가까울수록 투명합니다
        strokeStyle: "dashed", // 선의 스타일 입니다
        fillColor: "#CFE7FF", // 채우기 색깔입니다
        fillOpacity: 0.3, // 채우기 불투명도 입니다
      });

      // 지도에 원을 표시합니다
      this.circle.setMap(this.map);
    },

    // 장소검색이 완료됐을 때 호출되는 콜백함수 입니다
    placesSearchCB(data, status) {
      if (status === kakao.maps.services.Status.OK) {
        // 정상적으로 검색이 완료됐으면 지도에 마커를 표출합니다
        this.displayPlaces(data);
      } else if (status === kakao.maps.services.Status.ZERO_RESULT) {
        // 검색결과가 없는경우 해야할 처리가 있다면 이곳에 작성해 주세요
      } else if (status === kakao.maps.services.Status.ERROR) {
        // 에러로 인해 검색결과가 나오지 않은 경우 해야할 처리가 있다면 이곳에 작성해 주세요
      }
    },

    // 지도에 마커를 표출하는 함수입니다
    displayPlaces(places) {
      // 몇번째 카테고리가 선택되어 있는지 얻어옵니다
      // 이 순서는 스프라이트 이미지에서의 위치를 계산하는데 사용됩니다
      var order = document
        .getElementById(this.currCategory)
        .getAttribute("data-order");

      for (var i = 0; i < places.length; i++) {
        // 마커를 생성하고 지도에 표시합니다
        var marker = this.addMarker(
          new kakao.maps.LatLng(places[i].y, places[i].x),
          order
        );

        // 마커와 검색결과 항목을 클릭 했을 때
        // 장소정보를 표출하도록 클릭 이벤트를 등록합니다
        ((marker, place) => {
          kakao.maps.event.addListener(marker, "click", () => {
            this.displayPlaceInfo(place);
          });
        })(marker, places[i]);
      }
    },

    // 마커를 생성하고 지도 위에 마커를 표시하는 함수입니다
    addMarker(position, order) {
      var imageSrc =
          "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/places_category.png", // 마커 이미지 url, 스프라이트 이미지를 씁니다
        imageSize = new kakao.maps.Size(27, 28), // 마커 이미지의 크기
        imgOptions = {
          spriteSize: new kakao.maps.Size(72, 208), // 스프라이트 이미지의 크기
          spriteOrigin: new kakao.maps.Point(46, order * 36), // 스프라이트 이미지 중 사용할 영역의 좌상단 좌표
          offset: new kakao.maps.Point(11, 28), // 마커 좌표에 일치시킬 이미지 내에서의 좌표
        },
        markerImage = new kakao.maps.MarkerImage(
          imageSrc,
          imageSize,
          imgOptions
        ),
        marker = new kakao.maps.Marker({
          position: position, // 마커의 위치
          image: markerImage,
        });

      marker.setMap(this.map); // 지도 위에 마커를 표출합니다
      this.markers.push(marker); // 배열에 생성된 마커를 추가합니다

      return marker;
    },

    // 지도 위에 표시되고 있는 마커를 모두 제거합니다
    removeMarker() {
      for (var i = 0; i < this.markers.length; i++) {
        this.markers[i].setMap(null);
      }
      this.markers = [];

      if (this.circle) this.circle.setMap(null);
    },

    // 클릭한 마커에 대한 장소 상세정보를 커스텀 오버레이로 표시하는 함수입니다
    displayPlaceInfo(place) {
      var content =
        '<div class="placeinfo">' +
        '   <a class="title" href="' +
        place.place_url +
        '" target="_blank" title="' +
        place.place_name +
        '">' +
        place.place_name +
        "</a>";

      if (place.road_address_name) {
        content +=
          '    <span title="' +
          place.road_address_name +
          '">' +
          place.road_address_name +
          "</span>" +
          '  <span class="jibun" title="' +
          place.address_name +
          '">(지번 : ' +
          place.address_name +
          ")</span>";
      } else {
        content +=
          '    <span title="' +
          place.address_name +
          '">' +
          place.address_name +
          "</span>";
      }

      content +=
        '    <span class="tel">' +
        place.phone +
        "</span>" +
        "</div>" +
        '<div class="after"></div>';

      this.contentNode.innerHTML = content;
      this.placeOverlay.setPosition(new kakao.maps.LatLng(place.y, place.x));
      this.placeOverlay.setMap(this.map);
    },

    // 카테고리를 클릭했을 때 호출되는 함수입니다
    onClickCategory(event) {
      var target = event.currentTarget;
      var id = target.id;
      var className = target.className;

      this.placeOverlay.setMap(null);

      if (className === "on") {
        this.currCategory = "";
        this.changeCategoryClass();
        this.removeMarker();
      } else {
        this.currCategory = id;
        this.changeCategoryClass(target);
        this.searchPlaces();
      }
    },

    // 클릭된 카테고리에만 클릭된 스타일을 적용하는 함수입니다
    changeCategoryClass(el) {
      var category = document.getElementById("category"),
        children = category.children,
        i;

      for (i = 0; i < children.length; i++) {
        children[i].className = "";
      }

      if (el) {
        el.className = "on";
      }
    },

    // 지도가 이동할 떄 해당 법정동 코드를 받아서 백엔드 api 호출하여 해당 동 데이터만 받아옴
    async getCenterAroundHouseInfo(result, status) {
      if (status === kakao.maps.services.Status.OK) {
        for (var i = 0; i < result.length; i++) {
          // 법정동
          if (result[i].region_type === "B") {
            await this.getHouseList({
              dongCode: result[i].code,
              filterType: this.filterHouseType,
              filterBuildYear: this.filterBuildYear,
            }); // 동데이터 요청
          }
        }
      }
    },

    deleteHouseMarkers() {
      for (var i = 0; i < this.houseMarkers.length; i++) {
        this.houseMarkers[i].setMap(null);
      }
      this.houseMarkers = [];
    },

    moveMapByDong() {
      if (!this.dongCode) return;

      // 선택한 동으로 좌표 이동
      let sidoName = this.sidos.find((item) => item.value == this.sidoCode)
        .text;
      let gugunName = this.guguns.find((item) => item.value == this.gugunCode)
        .text;
      let dongName = this.dongs.find((item) => item.value == this.dongCode)
        .text;

      let address = `${sidoName} ${gugunName} ${dongName}`;

      new kakao.maps.services.Geocoder().addressSearch(
        address,
        (result, status) => {
          if (status === kakao.maps.services.Status.OK) {
            // 이동할 위도 경도 위치를 생성합니다
            var moveLatLon = new kakao.maps.LatLng(result[0].y, result[0].x);

            // 지도 중심을 부드럽게 이동시킵니다
            // 만약 이동할 거리가 지도 화면보다 크면 부드러운 효과 없이 이동합니다
            this.map.panTo(moveLatLon);
          }
        }
      );
    },

    gugunList() {
      this.CLEAR_GUGUN_LIST();
      this.CLEAR_DONG_LIST();
      this.gugunCode = null;
      if (this.sidoCode) this.getGugun(this.sidoCode);
    },

    dongList() {
      this.CLEAR_DONG_LIST();
      this.dongCode = null;
      if (this.gugunCode) this.getDong(this.gugunCode);
    },

    categoryRefresh() {
      this.searchPlaces();
    },

    checkHouseInterest() {
      for (let i = 0; i < this.interestHouseList.length; i++) {
        if (this.interestHouseList[i].house_id == this.house.houseId) {
          this.houseInterestInfo = this.interestHouseList[i];
          return true;
        }
      }
      for (let i = 0; i < this.interestOfficetelList.length; i++) {
        if (this.interestOfficetelList[i].house_id == this.house.houseId) {
          this.houseInterestInfo = this.interestOfficetelList[i];
          return true;
        }
      }
      return false;
    },

    addHouseInterest() {
      this.addInterestHouse({
        userid: this.userInfo.userid,
        houseid: this.house.houseId,
      });
    },

    removeHouseInterest() {
      this.deleteInterestHouse(this.houseInterestInfo);
    },

    resetFilter() {
      this.filterHouseType = "all";
      this.filterBuildYear = 0;
      this.refreshHouseList();
    },

    selectFilterHouseType(type) {
      this.filterHouseType = type;
      this.refreshHouseList();
    },

    selectFilterBuildYear(year) {
      this.filterBuildYear = year;
      this.refreshHouseList();
    },

    selectFilterHouseDealType(type) {
      this.filterHouseDealType = type;
      // 눌렀을 때 하우스 정보 세팅
      if (type == "all") {
        this.filteredHouseDeals = this.houseDeals;
        return;
      }

      let tempDeals = [];
      for (let i = 0; i < this.houseDeals.length; i++) {
        if (type == "sale") {
          if (this.houseDeals[i].dealType == "sale") {
            tempDeals.push(this.houseDeals[i]);
          }
        } else {
          if (this.houseDeals[i].dealType != "sale") {
            tempDeals.push(this.houseDeals[i]);
          }
        }
      }

      console.log(tempDeals);
      this.filteredHouseDeals = tempDeals;
    },

    refreshHouseList() {
      // 지도의 중심좌표를 얻어옵니다
      var coords = this.map.getCenter();
      // 주소-좌표 변환 객체를 생성합니다
      var geocoder = new kakao.maps.services.Geocoder();
      geocoder.coord2RegionCode(
        coords.getLng(),
        coords.getLat(),
        this.getCenterAroundHouseInfo
      );
    },

    locationLoadSuccess(pos) {
      // 현재 위치 받아오기
      let currentPos = new kakao.maps.LatLng(
        pos.coords.latitude,
        pos.coords.longitude
      );

      // 지도 이동(기존 위치와 가깝다면 부드럽게 이동)
      this.map.panTo(currentPos);
    },

    locationLoadError() {
      console.log("위치 정보를 가져오는데 실패했습니다.");
    },

    moveMapPos(lat, lng) {
      let pos = new kakao.maps.LatLng(lat, lng);

      // 지도 이동(기존 위치와 가깝다면 부드럽게 이동)
      this.map.panTo(pos);
    },

    searchName(event) {
      if (event.key == "Enter") return;
      this.getSearchWordList(this.searchWord);
    },
    moveMap() {
      if (!this.searchResultList.length) return;
      this.moveMapPos(this.searchResultList[0].y, this.searchResultList[0].x);
      this.searchWord = "";
      this.initResultList();
    },
    // 선택하면 해당 좌표로 이동
    selectSearchList(index) {
      this.moveMapPos(
        this.searchResultList[index].y,
        this.searchResultList[index].x
      );
      this.searchWord = "";
      this.initResultList();
    },

    ...mapActions(houseStore, [
      "getHouseList",
      "selectHouse",
      "initHouse",
      "getSido",
      "getGugun",
      "getDong",
      "initResultList",
      "getSearchWordList",
    ]),
    ...mapMutations(houseStore, [
      "CLEAR_SIDO_LIST",
      "CLEAR_GUGUN_LIST",
      "CLEAR_DONG_LIST",
    ]),
    ...mapActions(interestStore, ["addInterestHouse", "deleteInterestHouse"]),
  },
  computed: {
    headerStyle() {
      return {
        backgroundImage: `url(${this.header})`,
      };
    },
    ...mapState(houseStore, [
      "houses",
      "house",
      "houseDeals",
      "sidos",
      "guguns",
      "dongs",
      "searchResultList",
    ]),
    ...mapState(memberStore, ["userInfo"]),
    ...mapState(interestStore, ["interestHouseList", "interestOfficetelList"]),
  },
  watch: {
    houses(newHouses) {
      this.deleteHouseMarkers();
      for (var i = 0; i < newHouses.length; i++) {
        // 마커를 생성합니다
        const marker = new kakao.maps.Marker({
          position: new kakao.maps.LatLng(newHouses[i].lat, newHouses[i].lng),
          title: newHouses[i].houseName,
          clickable: true,
        });

        // 마커에 클릭 이벤트를 등록합니다
        kakao.maps.event.addListener(marker, "click", () => {
          // 눌렀을 때 하우스 정보 세팅
          this.selectHouse(marker.getTitle());
        });

        // 마커가 지도 위에 표시되도록 설정합니다
        marker.setMap(this.map);
        this.houseMarkers.push(marker);
      }
    },
    houseDeals(newHouseDeals) {
      this.filteredHouseDeals = newHouseDeals;
      // console.log(this.filteredHouseDeals);
    },
  },
};
</script>

